<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Login form</v-toolbar-title>
            <v-spacer />
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                label="Login"
                name="login"
                prepend-icon="person"
                type="text"
                v-model="userName"
              />

              <v-text-field
                id="password"
                label="Password"
                name="password"
                prepend-icon="lock"
                type="password"
                v-model="password"
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn :to="{name:'sign-up'}" active-class text color="pink">sign up</v-btn>
            <v-btn @click="onSubmit" color="primary">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-alert
      dismissible
      :value="authStatus=='error'"
      type="error"
      transition="scale-transition"
    >{{authMessage}}</v-alert>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  props: {
    source: String
  },
  data() {
    return {
      userName: "pouya.samie@gmail.com",
      password: ""
    };
  },
  computed: {
    ...mapGetters(["isLoggedIn", "authMessage", "authStatus"])
  },
  methods: {
    ...mapActions(["login"]),
    onSubmit(e) {
      e.preventDefault();
      this.login({ userName: this.userName, password: this.password });
    }
  }
};
</script>

